@use '../foundation/foundation';
@use '../foundation/variables';

.p-footer{
    background-color: variables.$blue;
    padding:18px 0; 
    color: white;
    text-align: center;
    &__navi{
        ul{
            @include foundation.media_tablet{
                display: flex;
                flex-direction: row;
                justify-content: center;
            }
            li{
                margin: 20px 0;
                @include foundation.media_tablet{
                    margin:0 30px 30px 0;
                    font-size: 1.6rem;
                }
            }
        }
    }
    a{
        font-family: MOBO, 'Montserrat', sans-serif, "Segoe UI", "Helvetica Neue", HelveticaNeue, YuGothic, "Yu Gothic Medium", "Yu Gothic", Verdana, Meiryo, sans-serif;
        font-size: 1.6rem;
    }
}
